export default{
  data() {
    return {
      title: '',
      token: '',
      showDel: false,
      code_word: '获取验证码',
      mobile: '',
      code: '',
      password: '',
      check_code: '',
      showInfo: false,
      ishide: false
    }
  },
  // onLoad(option) {
  //   console.log(option.token)
  //   this.token = option.token
  // },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    onInput(e) {
      let value = e.detail.value
      if(value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    sureAction() {
      if(!this.check_code) {
        uni.showToast({
          title: '请先获取手机验证码',
          icon: 'none'
        })
        return
      }
      if(this.check_code !== this.code) {
        uni.showToast({
          title: '验证码不正确',
          icon: 'none'
        })
        return
      }
      if(!this.password) {
        uni.showToast({
          title: '请输入密码',
          icon: 'none'
        })
        return
      }
      // if(this.check_code === this.code && this.check_code) {
      //   let data = {
      //     mobile: this.mobile,
      //     password: this.password
      //   }
      //   // console.log(JSON.stringify(data))
      //   this.$http({
      //     url: this.$api.otherregister,
      //     method: 'POST',
      //     data: data,
      //     cb: (err, res) => {
      //       if(!err && res.code === 1) {
      //         uni.showToast({
      //           title: '绑定手机号成功',
      //           icon: 'none'
      //         })
      //         uni.switchTab({
      //           url: '../index/index'
      //         })
      //       } else if (res.code === 0 && res.msg){
      //         uni.showToast({
      //           title: res.msg,
      //           icon: 'none'
      //         })
      //       } if(err) {
      //         uni.showToast({
      //           title: '绑定手机号失败',
      //           icon: 'none'
      //         })
      //       }
      //     }
      //   })
      // }
    },
    getCode() {
      let value = /^1[3456789]\d{9}$/.test(this.mobile)
      let that = this
      if (!value){
        uni.showToast({
          title: '请输入正确的手机号码',
          icon: 'none'
        })
        return
      }
      this.$http({
        url: this.$api.sendcode,
        method: 'POST',
        data: {
          mobile: this.mobile
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            that.check_code = res.data.info.code.toString()
            that.code_word = 120
            that.showInfo = true
            setInterval(function() {
              if (that.code_word > 0) {
                that.code_word--
              }
              if (that.code_word === 0) {
                that.check_code = ''
                that.showInfo = false
                return
              }
            }.bind(that), 1000)
          } else {
            uni.showToast({
              title: '手机验证码获取失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
